import React, { useState, useEffect } from "react";
import img from "./index.jpeg";
import axios from "axios";

function Home() {
  useEffect(() => {
    axios.get("/ok").then((data) => {
      console.log("category list is", data.data);
    });
  }, []);
  return (
    <div>
      <h1 className="head">Movie-Mate</h1>
      <div className="blog-container">
        <img src={img} className="image" />
        <div className="details">
          <div>
            <h1>Name</h1>
            <h4>Release Date</h4>
            <h4>Genre: | Duration: | Rating: | Language:</h4>
            <p>Description</p>
          </div>
          <div>
            <h4>Cast</h4>
            <h4>Director</h4>
            <h6>Post Id:</h6>
            <button id="button">Watch Trailer</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;
