import React, { Component } from "react";

class CreateBlog extends Component {
  render() {
    return (
      <div>
        <div className="create-blog">
          <h1>Create Blog</h1>
          <div className="create-blog-details">
            <form action="" method="post" id="create">
              <input
                type="text"
                className="form-text"
                placeholder="Name of the Movie"
              />
              <input
                type="text"
                className="form-text"
                placeholder="Release Date"
              />
              <input type="text" className="form-text" placeholder="Genre" />
              <input type="text" className="form-text" placeholder="Language" />
              <input type="text" className="form-text" placeholder="Duration" />
              <input type="text" className="form-text" placeholder="Rating" />
              <input
                type="text"
                className="form-text"
                placeholder="Description"
              />
              <input type="text" className="form-text" placeholder="Cast" />
              <input type="text" className="form-text" placeholder="Director" />
              <input
                type="text"
                className="form-text"
                placeholder="Trailer Link"
              />
              <input
                type="file"
                className="form-upload"
                name="filename"
              ></input>
              <input type="submit" className="upload-btn" value="Upload" />
            </form>

            <input
              type="submit"
              id="button"
              className="submit"
              value="Create"
              form="create"
            />
          </div>
        </div>
      </div>
    );
  }
}

export default CreateBlog;
