import React, { Component } from "react";

class SignUp extends Component {
  render() {
    return (
      <div>
        <div className="container">
          <h2 id="title">Sign Up</h2>
          <div className="items">
            <form action="" method="post" id="signup">
              <input type="text" id="form-text" placeholder="Full Name" />
              <input type="email" id="form-text" placeholder="Email" />
              <input
                type="password"
                id="form-password"
                placeholder="Password"
              />
            </form>
            <button id="button" form="signup">
              Sign Up
            </button>
            <h2 id="text"> Already a User?</h2>
            <a id="sign" type="submit" href="#">
              Sign In
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default SignUp;
